import React from 'react';
import { Link } from 'react-router-dom';
import './PaymentThanks.css';

const PaymentThanks = () => {

    const pathId = window.location.pathname.substr(25);

    const displayMessage = () => {
        switch (pathId) {
            case 'payOnline':
                return (
                    <>
                        <h1>Dzikujemy</h1>
                        <p>Twoja patno powioda si.</p>
                        <Link to="/delivery">Strona gwna</Link>
                    </>
                );
            case 'payCardOnDelivery':
                return(
                    <>
                        <h1>Dzikujemy</h1>
                        <p>Twoje zamwienie jest w trakcie realizacji.</p>
                        <p>Szacowanych czas oczekiwania to 30 minut.</p>
                        <Link to="/delivery">Strona gwna</Link>
                    </>
                );
            case 'payInRestaurant':    
                return(
                    <>
                        <h1>Dzikujemy</h1>
                        <p>Twoje zamwienie jest w trakcie realizacji.</p>
                        <p>Szacowanych czas oczekiwania to 10 minut.</p>
                        <p>Twoje zamwienie bdzie na Ciebie czeka przez 40 minut w wybranej wczeniej restauracji.</p>
                        <Link to="/delivery">Strona gwna</Link>
                    </>
                );
            default:
                break;
        }
    }

    return(
        <div className="paymentThanks">
            { displayMessage() }
        </div>
    );
}

export default PaymentThanks;